/** initial setup **/
.scroll-pane {
  position: relative;
  overflow: hidden;
}
.scroll-pane > .scroll-content {
  position: absolute;
  overflow: scroll;
  overflow-x: hidden;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.scroll-pane > .scroll-content:focus {
  outline: thin dotted;
}
.scroll-pane > .scroll-content::-webkit-scrollbar {
  display: none;
}
.scroll-pane > .scroll-track {
  background: rgba(0, 0, 0, 0.04);
  position: absolute;
  width: 8px;
  right: 0;
  top: 0;
  bottom: 0;
  /* Target only IE7 and IE8 with this hack */
  visibility: hidden\9;
  opacity: .01;
  transition: .2s;
}
.scroll-pane > .scroll-track > .scroll-thumb {
  background: #757575;
  background: rgba(0, 0, 0, 0.12);
  position: relative;
  margin: 0;
  border-radius: 8px;
}
.scroll-pane:hover > .scroll-track,
.scroll-track.active,
.scroll-track.flashed {
  /* Target only IE7 and IE8 with this hack */
  visibility: visible\9;
  opacity: 0.99;
}
.has-scrollbar > .scroll-content::-webkit-scrollbar {
  display: block;
}
