* {
    box-sizing: border-box;
}

#topnav.navbar-indigo {
    background: #3f51b5;
}

#topnav {
    margin: 0;
    border: none;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,.16),0 2px 10px 0 rgba(0,0,0,.12) !important;
}

.navbar-fixed-top {
    top: 0;
    border-width: 0 0 1px;
}

.navbar-fixed-top {
    width: 100%;
    position: fixed !important;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1200;
}

#wrapper {
    display: table;
    width: 100%;
    table-layout: fixed;
    overflow: auto;
    height: 100%;
    position: relative;
}

#layout-static {
    display: table-row;
    height: 100%;
}

.static-sidebar-wrapper, .static-sidebar {
    width: 240px;
    transition-duration: 300ms;
    position: fixed;
    top: 64px;
    left: 0;
    bottom: 0;
    height: 100vh;
    z-index: 10000;
    background: #f5f5f5 !important;
}

.static-content-wrapper {
    background: #ffffff !important;
}

.static-content-wrapper {
    position: relative;
}

.static-sidebar-wrapper, .static-content-wrapper {
    vertical-align: top;
}

.static-content-wrapper {
    display: table-cell;
    width: 100%;
    padding-left: 240px;
    transition-duration: 300ms;
}

.menu-splitter {
    height: 1px;
    background: #ccc;
    margin: 0;
    margin-top: -1px;
    
}

.menu-item {
    position: relative;
    height: 46px;
    width: 100%;
    display: block;
    clear: both;
    color: #000;
    font-size: 16px;
    line-height: 48px !important;
    margin-top: -1px;
    border-top: solid 1px transparent;
    border-bottom: solid 1px transparent;
}

    .menu-item:hover {
        color: #fff !important;
        background-color: #fff;
        border-bottom-color: #ccc;
        border-top-color: #ccc;
    }

    .menu-item:active {
        color: #fff !important;
        background-color: #c0c0c0;
        border-bottom-color: #fff;
        border-top-color: #fff;
    }

.menu-icon {
    position: absolute;
    left: 0;
    top: 0; line-height: 46px !important;
    height: 38px;
    margin: auto;
    width: 48px;
    font-size: 26px !important;
    margin-left: 16px;
    transition-duration: 300ms;
}

.menu-text {
    position: absolute;
    left: 58px;
    top: -2px;
    margin: auto;
    width: 175px;
    transition-duration: 300ms;
}

.menu-item:link .menu-text, .menu-item:visited .menu-text {
    color: #000000 !important;
}

.menu-item:hover .menu-text {
    color: #039be5 !important;
}

.menu-item:active .menu-text {
    color: #fff !important;
}



.menu-item:link .menu-icon, .menu-item:visited .menu-icon {
    color: #000000 !important;
}

.menu-item:hover .menu-icon {
    color: #039be5 !important;
}

.menu-item:active .menu-icon {
    color: #fff !important;
}

 .widget{
     transition-duration: 300ms;
 }
@media (max-width: 640px) {

    .menu-text {
        opacity: 0;
    }

    .menu-icon {
        left: 2px;
    }

    .static-sidebar-wrapper, .static-sidebar {
        width: 64px !important;
        overflow: hidden;
    }

    .static-content-wrapper {
        display: table-cell;
        width: 100%;
        background: #fff;
        padding-left: 64px;
    }
    .widget{
        height:0!important;
    }
}


.navbar-brand.navbar-blue.toggle-icon {
    width: 64px !important;
}

.navbar-brand.navbar-blue.toggle-icon > .show-on-collapse {
    opacity: 1 !important;
}
.navbar-brand.navbar-blue.toggle-icon > .img-white {
    opacity:0 !important;
    
}


.static-sidebar-wrapper.toggle-icon, .static-sidebar.toggle-icon {
        width: 64px !important;
        overflow: hidden;
    }

    .static-content-wrapper.toggle-icon {
        display: table-cell; 
        width: 100%;
        background: #fff;
        padding-left: 64px;
    }

    .menus.toggle-icon  > div > .menu-item > .menu-text {
        opacity: 0;
    }

    .menus.toggle-icon > div  > .menu-item > .menu-icon {
        left: 2px;
    }

    .static-content-wrapper.toggle-icon {
        display: table-cell;
        width: 100%;
        background: #fff;
        padding-left: 64px;
    }

